.btn {
  border: 2px solid;
  border-radius: 25px;
  outline: 0;
  color: $btn-main-color;
  font-size: 1.5rem;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
  overflow: hidden;
  position: relative;

  @include mobile {
    font-size: 1rem;
  }

  &__txt,
  &__icon {
    display: block;
    transition: transform 0.5s ease-in-out;
  }

  &__txt {
    padding: 1rem 2rem;
  }
  &__icon {
    @include flex(center, center);
    font-size: 2rem;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transform: translateX(-100%);
  }

  &-animate:hover &__txt {
    transform: translateX(-100%);
  }

  &-animate:hover &__icon {
    transform: translateX(0);
  }

  &-sm {
    font-size: 1rem;
  }

  &sm &__txt {
    padding: 0.7rem 1.5rem;
  }
}
